import React from 'react';
import HDIImg from '../../../assets/projects/emermodels/hdi.png';
import OCNImg from '../../../assets/projects/emermodels/ocn_extinguisher.png';
import OKPImg from '../../../assets/projects/emermodels/okp_extinguisher.png';
import ULDImg from '../../../assets/projects/emermodels/uld.png';

function EmerModels() {
	return (
		<div>
			<img src={HDIImg} alt=''/>
			<img src={OKPImg} alt=''/>
			<img src={OCNImg} alt=''/>
			<img src={ULDImg} alt=''/>
		</div>
	);
}

export default EmerModels;